	methods: {
		goToDetails(id){//跳转详情页
			console.log(1111);
			uni.navigateTo({
				url:'/pages/details/details?id='+id
			})
		},
		goToConfirmorder(index){//跳转确认订单页面
			let arr = [];
			arr.push(this.orderArr[index])
			uni.navigateTo({
				url:'/pages/confirmorder/confirmorder?arr='+JSON.parse(arr)
			})
		},
		goToReview(id){//跳转评论页面
			uni.navigateTo({
				url:'/pages/review/review?id='+id+'&index=0'
			})
		},
		detele(index,type){//删除或取消订单
			let str = type=='0'?'删除':'取消'
			uni.showModal({
				title:str,
				content:`确认${str}这个订单吗?`,
				success:res=>{
					if(res.confirm){
						this.orderArr.splice(index,1);
					}
				}
			})
		}
	}
